.scroll-wrap {
  overflow: hidden;
  margin-bottom: 100px;
  letter-spacing: -.05em;
  .banner-wrap-left {
    overflow: hidden;
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20% 20% 20% 20% 20% 20%;
    margin-left: -60%; }
  .left-to-right-fast {
    animation: left-to-right 35s linear 0s infinite; }
  .left-to-right-medium {
    animation: left-to-right 40s linear 0s infinite; }
  .left-to-right-slow {
    animation: left-to-right 42s linear 0s infinite; }
  .left-to-right-very-slow {
    animation: left-to-right 44s linear 0s infinite; }
  .banner-wrap-right {
    overflow: hidden;
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20% 20% 20% 20% 20% 20%;
    margin-right: -60%; }
  .right-to-left-fast {
    animation: right-to-left 35s linear 0s infinite; }
  .right-to-left-medium {
    animation: right-to-left 40s linear 0s infinite; }
  .right-to-left-slow {
    animation: right-to-left 42s linear 0s infinite; }
  .right-to-left-very-slow {
    animation: right-to-left 44s linear 0s infinite; } }
%banner {
  font-size: 2.8vw;
  font-weight: 400; }
.banner {
  @extend %banner; }
.banner-stroke {
  @extend %banner;
  @include stroke-light-grey; }
